.section {
  margin: 0 auto;
  position: relative;
  width: 100%;
  @include clearfix;
  background-color: #f5f8fa;
  display: flex;
  justify-content: center;
  max-width: 1440px;
  .content {
    width: calc((100% / 12) * 9);
    background: url("../images/kitchen.svg") no-repeat;
    background-position: bottom;
    box-sizing: border-box;
    padding-left: 16px;
    padding-right: 16px;
    &_navigation {
      display: flex;
      flex-wrap: wrap;
      .a_navigation {
        width: 100%;
        padding: 10px 15px;
      }
    }
  }
  .section__content {
    width: unset;
  }
  &__title {
    .button {
      position: relative;
      display: flex;
      justify-content: center;
      align-items: center;
      font-family: "Exo2-Semi";
      font-size: 12px;
      font-weight: 600;
      font-style: normal;
      font-stretch: normal;
      line-height: 1.25;
      letter-spacing: 0.8px;
      color: #ffffff;
      text-decoration: none;
      text-transform: uppercase;
      padding: 0.5em 1em;
      outline: none;
      border-radius: 4px;
      background-color: #4580e6;
      border: solid 1px #394b5985;
      transition: 0.2s ease-in-out;
      margin-right: 16px;
      &:active {
        top: 1px;
        opacity: 0.6;
        border-radius: 4px;
        background-image: linear-gradient(to bottom, #ffffff, #637381);
        box-shadow: 0 0 1px rgba(0, 0, 0, 0.5) inset,
          0 2px 3px rgba(0, 0, 0, 0.5) inset, 0 1px 1px rgba(255, 255, 255, 0.1);
      }
      &__plus {
        margin-left: 20px;
        width: 16px;
        height: 16px;
        background-size: contain;
        background-image: url("https://icongr.am/feather/plus.svg?color=ffffff");
      }
    }
    .date {
      width: 100%;
      border-radius: 4px;
      box-shadow: inset 0 1px 2px 0 rgba(16, 22, 26, 0.2);
      border: solid 1px #d8e1e8;
      text-transform: uppercase;
      font-family: "Exo2-Semi";
      font-size: 12px;
      font-weight: 600;
      font-style: normal;
      font-stretch: normal;
      line-height: 1.33;
      letter-spacing: 0.8px;
      color: #bfccd6;
      padding-left: 12px;
      &::-webkit-calendar-picker-indicator {
        width: 13px;
        height: 13px;
        background: url("https://icongr.am/fontawesome/calendar.svg?color=bfccd6")
          no-repeat center;
        background-size: contain;
        color: transparent;
        margin-right: 12px;
      }
      &::-webkit-clear-button {
        display: none;
      }
      &::-webkit-inner-spin-button {
        display: none;
      }
    }
    .pagination {
      &__items {
        display: flex;
        margin-top: 24px;
        margin-bottom: 16px;
        justify-content: space-between;
      }
      &__select {
        width: 100%;
        max-width: 160px;
        color: #394b59;
      }
      &__h4 {
        font-family: "SourceSansPro";
        font-size: 16px;
        font-weight: 300;
        font-style: normal;
        font-stretch: normal;
        line-height: 1.5;
        letter-spacing: normal;
        text-align: center;
        color: #394b59;
        margin-bottom: 0;
        margin-left: 5%;
      }
      &__span {
        font-weight: 600;
        color: #293742;
      }
    }
  }
  &__text {
    display: flex;
    align-items: center;
    font-family: "Exo2-Regular";
    font-size: 24px;
    font-weight: normal;
    font-style: normal;
    font-stretch: normal;
    line-height: 1.17;
    letter-spacing: normal;
    color: #293742;
    margin-top: 24px;
    margin-bottom: 12px; // index_2.html
    .text__a {
      font-family: "Exo2-Semi";
      font-size: 24px;
      font-weight: 600;
      font-style: normal;
      font-stretch: normal;
      line-height: 1.17;
      letter-spacing: normal;
      color: #4580e6;
      margin-left: 8px;
      margin-right: 4px;
    }
    &__img {
      width: 24px;
      height: 24px;
    }
  }
  &__subtitle {
    font-family: "Exo2-Regular";
    font-size: 20px;
    font-weight: normal;
    font-style: normal;
    font-stretch: normal;
    line-height: 1.2;
    letter-spacing: normal;
    color: #293742;
    margin-top: 40px;
    margin-bottom: 16px;
    width: 100%;
  }
  &__items {
    display: flex;
    flex-wrap: wrap;
    padding-left: 16px;
  }
  &__list {
    width: 18%;
    display: flex;
    flex-wrap: wrap;
    margin-right: 9.333333333333333%;
    height: 8%;
    &:last-child {
      margin-right: 0;
    }
  }
  &__link {
    width: 100%;
    font-family: "Exo2-Regular";
    font-size: 16px;
    font-weight: normal;
    font-style: normal;
    font-stretch: normal;
    line-height: 1.5;
    letter-spacing: normal;
    color: #2b95d6;
    margin-top: 12px;
    margin-bottom: 12px;
    height: 8.333333333333333%;
    &:first-child {
      margin-top: 0;
    }
    &:last-child {
      margin-bottom: 0;
    }
  }
  &__select {
    display: flex;
    height: 40px;
  }
  .select {
    border-radius: 4px;
    box-shadow: inset 0 1px 2px 0 rgba(16, 22, 26, 0.2);
    background-color: #ffffff;
    border: solid 1px #d8e1e8;
    padding-left: 15px;
    font-family: "Exo2-Semi";
    font-size: 12px;
    text-transform: uppercase;
    font-weight: 600;
    font-style: normal;
    font-stretch: normal;
    line-height: 1.33;
    letter-spacing: 0.8px;
    color: #bfccd6;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    background: url("../images/chevron-down.svg") 96% / 16px no-repeat white;
    .option {
      font-family: "Exo2-Semi";
      color: #394b59;
      font-size: 12px;
      font-weight: 600;
      font-style: normal;
      font-stretch: normal;
      line-height: 1.33;
      letter-spacing: 0.8px;
      color: #394b59;
    }
    &__score {
      width: 100%;
      max-width: 115px;
    }
    &__category {
      width: 100%;
      max-width: 224px;
    }
    &_city {
      width: 100%;
      max-width: 160px;
    }
  }
  .form {
    position: relative;
    border-radius: 4px;
    box-shadow: inset 0 1px 2px 0 rgba(16, 22, 26, 0.2);
    background-color: #ffffff;
    border: solid 1px #d8e1e8;
    width: 100%;
    display: flex;
    align-items: center;
    input,
    button {
      border: none;
      outline: none;
      background: transparent;
    }
    .input {
      padding-left: 5px;
      font-family: "SourceSansPro";
      font-size: 14px;
      font-weight: normal;
      font-style: normal;
      font-stretch: normal;
      line-height: 1.14;
      letter-spacing: normal;
      color: #bfccd6;
      opacity: 0.6;
    }
    &__img {
      width: 16px;
      height: 16px;
      cursor: pointer;
      &_settings {
        margin: 0 0 0 auto;
      }
      &_magnifyingglass {
        margin-right: 12px;
        margin-left: 12px;
      }
    }
  }
  &__table {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    .table {
      &__title,
      &__block {
        width: 100%;
        border-radius: 4px;
        box-shadow: 8px 8px 16px 0 rgba(191, 204, 214, 0.2);
        background-color: #ffffff;
        border: solid 1px #d8e1e8;
        height: 48px;
        align-items: center;
        display: flex;
      }
      &__block {
        border-top: none;
        .span {
          font-family: "SourceSansPro";
          font-size: 14px;
          font-weight: normal;
          font-style: normal;
          font-stretch: normal;
          line-height: 1.43;
          letter-spacing: normal;
          height: 20px;
          &__cost {
            margin-left: 5px;
          }
          &__plus {
            margin: 0 5px;
          }
          &__cost,
          &__plus {
            color: #bfccd6;
          }
          &__brand {
            color: #293742;
            padding-left: 24px;
          }
          &__model {
            color: #4580e6;
            font-weight: 600;
            padding-left: 32px;
          }
          &__availability {
            font-size: 10px;
            font-weight: bold;
            line-height: 1.2;
            letter-spacing: 0.8px;
            text-align: center;
            border-radius: 3px;
            background-color: #2cb76b;
            font-weight: bold;
            text-transform: uppercase;
            color: #ffffff;
            padding-left: 0;
            justify-content: center;
          }
          &__sale {
            color: #293742;
            padding-left: 0.4%;
          }
          &__purchase {
            color: #293742;
            padding-left: 0.5%;
          }
          &__commission {
            color: #293742;
            padding-left: 0.3%;
          }
          &__formula {
            color: #293742;
            font-weight: 600;
            text-transform: uppercase;
            padding-left: 0.4%;
          }
          &__unid {
            color: #293742;
            display: flex;
            justify-content: flex-end;
          }
          &__math {
            background-image: url("https://icongr.am/feather/plus.svg?color=d8e1e8");
            min-width: 20px;
            height: 20px;
            background-size: cover;
            position: absolute;
            right: 16px;
            &:hover {
              background-image: url("https://icongr.am/feather/plus.svg?color=007ace");
            }
          }
        }
      }
    }
    .span {
      font-family: "Exo2-Semi";
      font-size: 14px;
      font-weight: 600;
      font-style: normal;
      font-stretch: normal;
      line-height: 1;
      letter-spacing: normal;
      color: #bfccd6;
      display: flex;
      align-items: center;
      &__brand {
        padding-left: 22px;
        width: 11%;
      }
      &__model {
        width: 11.45038167938931%;
        padding-left: 16px;
        &:hover {
          color: #394b59;
        }
      }
      &__availability {
        width: 7.6%;
        margin-left: 4.8%;
      }
      &__sale {
        width: 9%;
        font-weight: bold;
        margin-left: 7.7%;
        &:hover {
          color: #394b59;
        }
      }
      &__purchase {
        width: 8%;
        margin-left: 2.7%;
      }
      &__commission {
        width: 9%;
        margin-left: 3.3%;
      }
      &__formula {
        width: 11%;
        margin-left: 7.2%;
      }
      &__unid {
        width: 6.488549618320611%;
        padding-left: 1.2%;
      }
      &__bars {
        width: 10px;
        height: 12px;
        object-fit: contain;
        background-image: url("https://icongr.am/octicons/three-bars.svg?color=bfccd6");
        background-repeat: no-repeat;
        background-size: cover;
        &:hover {
          background-image: url("https://icongr.am/octicons/three-bars.svg?color=4580e6");
        }
      }
      &__arrows {
        width: 14px;
        height: 14px;
        object-fit: contain;
        background-image: url("https://icongr.am/entypo/select-arrows.svg?color=bfccd6");
        background-repeat: no-repeat;
        background-size: cover;
        margin-left: 10px;
        &:hover {
          background-image: url("https://icongr.am/entypo/select-arrows.svg?color=4580e6");
        }
      }
    }
  }
  &__nav {
    display: flex;
    align-items: center;
    border-bottom: 4px solid #d8e1e8;
    border-radius: 4px;
    width: 100%;

    .nav {
      &__text {
        font-family: "Exo2-Semi";
        font-size: 16px;
        font-weight: 600;
        font-style: normal;
        font-stretch: normal;
        line-height: 1.25;
        letter-spacing: normal;
        color: #bfccd6;
        padding: 12px 16px;
        margin-bottom: -4px;
        border-bottom: 4px solid #bfccd600;
        &_span {
          font-weight: 600;
        }
      }
      &__download {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 40px;
        height: 32px;
        border-radius: 4px;
        border: solid 1px #d8e1e8;
        margin: 0 8px 0 auto;
        background-color: rgba(216, 225, 232, 0.6);
        .span__download {
          background-image: url("https://icongr.am/entypo/download.svg?color=bfccd6");
          width: 16px;
          height: 16px;
          background-size: cover;
        }
      }
    }

    .active {
      color: #30404d;
      padding: 12px 16px !important;
      border-bottom: 4px solid #007ace;
      span {
        color: #4580e6;
      }
    }

    .pull {
      background-color: #4580e6;
      .span__download {
        background-image: url("https://icongr.am/entypo/download.svg?color=ffffff");
      }
    }
  }
  &__news {
    width: calc((100% / 12) * 3);
    box-sizing: border-box;
    padding-left: 16px;
    .news {
      &__title {
        display: flex;
        margin-top: 27px;
        margin-bottom: 12px;
      }
      &__h2 {
        font-family: "Exo2-Regular";
        font-size: 20px;
        font-weight: normal;
        font-style: normal;
        font-stretch: normal;
        line-height: 1.2;
        letter-spacing: normal;
        color: #293742;
        margin: 0;
      }
      &__hide {
        display: flex;
        margin: 0 0 0 auto;
        align-items: center;
        font-family: "Exo2-Bold";
        font-size: 12px;
        font-weight: bold;
        font-style: normal;
        font-stretch: normal;
        line-height: 1.33;
        letter-spacing: 0.4px;
        color: #bfccd6;
        text-transform: uppercase;
        .img {
          width: 20px;
          height: 20px;
          object-fit: contain;
          margin-right: 16px;
        }
      }
      &__blocks {
        border-radius: 4px;
        background-color: #ffffff;
        border: solid 1px #ebf1f5;
        padding-top: 24px;
        padding-left: 24px;
        padding-right: 32px;
        border-bottom: 1px solid white;
        border-top: 1px solid white;
        &:first-child {
          border-top: #ebf1f5;
        }
        &_index_5 {
          .section__news__block {
            &_h5 {
              &_span {
                font-family: "SourceSansPro";
                font-size: 14px;
                font-weight: 600;
                font-style: normal;
                font-stretch: normal;
                line-height: 1.29;
                letter-spacing: normal;
                color: #4580e6;
              }
            }
            &_h6 {
              text-overflow: ellipsis;
              overflow: hidden;
              white-space: nowrap;
            }
          }
        }
      }
      &__h6 {
        font-family: "Exo2-Regular";
        font-size: 12px;
        font-weight: normal;
        font-style: normal;
        font-stretch: normal;
        line-height: 1.33;
        letter-spacing: 0.4px;
        color: #bfccd6;
        margin: 0;
      }
      &__h5 {
        font-family: "Exo2-Semi";
        font-size: 12px;
        font-weight: 600;
        font-style: normal;
        font-stretch: normal;
        line-height: 1.33;
        letter-spacing: 0.4px;
        color: #8a9ba8;
        text-transform: uppercase;
        margin: 0 8px 0 8px;
      }
      &__block {
        display: flex;
      }
      &__img {
        width: 13px;
        height: 13px;
      }
      &__h3 {
        font-family: "Exo2-Medium";
        font-size: 16px;
        font-weight: 500;
        font-style: normal;
        font-stretch: normal;
        line-height: 1.25;
        letter-spacing: normal;
        color: #30404d;
        margin: 4px 0 16px 0;
      }
      &__h4 {
        font-family: "SourceSansPro";
        font-size: 14px;
        font-weight: normal;
        font-style: normal;
        font-stretch: normal;
        line-height: 1.43;
        letter-spacing: normal;
        color: #394b59;
        text-overflow: ellipsis;
        overflow: hidden;
        white-space: nowrap;
        margin: 0 0 16px 0;
      }
      &__item {
        display: flex;
        flex-wrap: wrap;
        padding-left: 12px;
        margin-bottom: 16px;
        &:last-child {
          margin-bottom: 0;
        }
        .item {
          &__h5 {
            font-family: "Exo2-Semi";
            font-size: 12px;
            font-weight: 600;
            font-style: normal;
            font-stretch: normal;
            line-height: 1.33;
            letter-spacing: 0.4px;
            margin-bottom: 7px;
          }
          &__h6 {
            font-family: "SourceSansPro";
            opacity: 0.8;
            font-size: 12px;
            font-weight: normal;
            font-style: normal;
            font-stretch: normal;
            line-height: 1.33;
            letter-spacing: normal;
            color: #394b59;
            margin-bottom: 0;
          }
        }
        &_pink {
          border-left: 4px solid #4580e6;
          h5 {
            color: #4580e6;
          }
        }
        &_red {
          border-left: 4px solid #f55656;
          h5 {
            color: #f55656;
          }
        }
        &_green {
          border-left: 4px solid #43bf4d;
          h5 {
            color: #43bf4d;
          }
        }
        &_yellow {
          border-left: 4px solid #f2b824;
          h5 {
            color: #f2b824;
          }
        }
      }
      &__border {
        margin-top: 31px;
        height: 1px;
        background-color: #d8e1e8;
      }
    }
  }
  &__sidebar {
    position: absolute;
    bottom: 0;
    height: 304px;
    border-radius: 4px;
    background-color: #f5f8fa;
    border: solid 1px #8a9ba8;
    max-width: 100%;
    z-index: 100;
    transition: 0.4s cubic-bezier(0.895, 0.03, 0.685, 0.22);
    .span {
      display: flex;
      &__name {
        width: 480px;
      }
      &__orders {
        width: 151px;
      }
      &__goods {
        width: 152px;
      }
      &__sale {
        width: 215px;
      }
      &__purchasing {
        width: 208px;
      }
      &__commission {
        width: 160px;
      }
      &__number {
        margin-left: 16px;
        font-size: 12px;
        font-weight: 600;
        font-style: normal;
        font-stretch: normal;
        line-height: 1.33;
        letter-spacing: normal;
        color: #454f5b;
      }
    }
    .sidebar {
      &__title {
        display: flex;
        align-items: center;
        height: 46px;
        border-radius: 4px;
        background-color: #ebf1f5;
        box-shadow: 0 1px 0 0 #bfccd6;
        .span {
          font-family: "Exo2-Regular";
          font-size: 12px;
          font-weight: normal;
          font-style: normal;
          font-stretch: normal;
          line-height: 1.25;
          letter-spacing: 0.8px;
          color: #8a9ba8;
          text-transform: uppercase;
          &__name {
            margin-left: 29px;
            font-family: "Exo2-Semi";
            font-size: 12px;
            font-weight: 600;
            font-style: normal;
            font-stretch: normal;
            line-height: 1.33;
            letter-spacing: 0.8px;
            color: #394b59;
          }
          &__close {
            background-image: url("https://icongr.am/entypo/chevron-down.svg?color=8a9ba8");
            width: 16px;
            height: 16px;
            background-size: cover;
            margin-left: 8px;
            &:hover {
              background-image: url("https://icongr.am/entypo/chevron-down.svg");
            }
          }
        }
      }
    }

    .item {
      display: flex;
      margin-top: 16px;
      &__failure {
        margin-top: 9px;
      }
      .span {
        box-sizing: border-box;
        font-family: "SourceSansPro";
        font-size: 14px;
        font-weight: normal;
        font-style: normal;
        font-stretch: normal;
        line-height: 1.43;
        letter-spacing: normal;
        color: #293742;
        &__name {
          width: 488px;
          margin-left: 22px;
        }
        &__orders {
          padding-left: 73px;
        }
        &__goods {
          padding-left: 74px;
        }
        &__sale {
          padding-left: 96px;
        }
        &__purchasing {
          padding-left: 99px;
        }
        &__commission {
          padding-left: 86px;
        }
      }
    }
    &__active {
      bottom: -257px;
      .span__close {
        background-image: url("https://icongr.am/entypo/chevron-up.svg?color=8a9ba8");
        width: 16px;
        height: 16px;
        background-size: cover;
        margin-left: 8px;
        &:hover {
          background-image: url("https://icongr.am/entypo/chevron-up.svg");
        }
      }
    }
  }
  &__model-page {
    .model-page {
      &__description {
        width: 100%;
        max-width: 328px;
        margin-right: 32px;
      }
      &__h2 {
        font-family: "Exo2-Regular";
        font-size: 32px;
        font-weight: normal;
        font-style: normal;
        font-stretch: normal;
        line-height: 1.38;
        letter-spacing: normal;
        color: #293742;
        margin-bottom: 0;
      }
      &__h5 {
        width: 100%;
        font-family: "Exo2-Regular";
        font-size: 12px;
        font-weight: normal;
        font-style: normal;
        font-stretch: normal;
        line-height: 1;
        letter-spacing: normal;
        color: #ced9e0;
        margin-bottom: 0;
        height: 50px;
      }
      &__check {
        display: flex;
        margin-left: 77px;
        margin-bottom: 10px;
        .span {
          &__availability {
            width: unset;
            margin-left: 8px;
            padding: 4px 8px;
            min-width: 75px;
          }
          &__sale {
            width: unset;
            margin-left: 8px;
          }
        }
        &_color {
          .slideThree,
          .span__availability {
            background-color: #f2b824 !important;
          }
        }
        .slideThree {
          width: 56px;
          height: 20px;
          background: #15b371;
          position: relative;
